<template>
  <div id="past_page" class="past_page">
    <div :style="{height: global.height+'px'}">
      <div class="coming_soon_text"
      >{{global.lang_pack.coming_soon+"\n"+dots}}
      </div>
    </div>


<!--    <Footer></Footer>-->
    <LangTransfer></LangTransfer>
    <Menu></Menu>
    <Logo :canBeHidden=false></Logo>
  </div>
</template>

<style lang="css" scoped>

div.past_page {
  position: absolute;
  background: #f7f7f7;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  overflow-x: hidden;
}

div.coming_soon_text{
  position: relative;
  font-family: blk_en;
  font-size: 100px;
  line-height: 1;
  text-align: center;
  cursor: default;
  white-space: pre-line;
  top: 50%;
  transform: translateY(-50%);
}


/*手机版样式*/
@media screen and (max-width: 540px) {
  div.coming_soon_text{
    position: relative;
    font-family: blk_en;
    font-size: 90rem;
    line-height: 1;
    text-align: center;
    cursor: default;
    white-space: pre-line;
    top: 50%;
    transform: translateY(-50%);
  }

}

</style>

<script>
import global from "../global";
import anime from "../../../modules/anime";
import Menu from "../../components/menu/Menu";
import LangTransfer from "../../components/lang_transfer/LangTransfer";
import Footer from "../../components/footer/Footer";
import Logo from "../../components/logo/Logo";


let has_expend_anime = false
//export default
export default {
  name: "About",
  data() {
    return {
      global: global,
      dots:"",
    };
  },
  components: {
    Menu,
    LangTransfer,
    Footer,
    Logo
  },
  beforeCreate() {
    //set documentElement root size and language package
    global.refresh_windows_size();
    global.set_lang_pack();
  },
  created() {
  },
  mounted() {
    Menu.methods.change_logo_color("#251714", "#efcd24", 1000);
    let that = this
    let num = 0
    setInterval(()=>{
      this.dots+="."
      if(this.dots.length > 6){
        this.dots = ""
      }
    }, 500)
  },
  destroyed() {
  },
  watch: {

  },
  methods: {

  }
};
</script>



